@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto-Regular"), url("../fonts/Roboto-Regular.ttf") format("truetype"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Roboto-Bold"), url("../fonts/Roboto-Bold.ttf") format("truetype"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Roboto-Light"), url("../fonts/Roboto-Light.ttf") format("truetype"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none;
  color: #111111; }

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none; }

input[type='number'],
input[type="number"]:hover,
input[type="number"]:focus {
  appearance: none;
  -moz-appearance: textfield; }

.button {
  height: 40px;
  background-color: #3366ff;
  padding: 8px 30px;
  color: #ffffff;
  line-height: 24px;
  font-weight: 400;
  border: 1px solid #ffffff;
  cursor: pointer;
  width: 100%; }

.active,
.button:hover {
  opacity: 80%; }

.page-body {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  width: 100%;
  margin: 0 auto;
  height: 100%; }
  .page-body__wrapper {
    padding: 0 30px; }

.page-body__title {
  font-size: 16px; }
  @media (min-width: 768px) {
    .page-body__title {
      font-size: 20px; } }

.list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  justify-content: flex-end;
  flex-direction: row-reverse;
  height: 300px; }

.list-inline {
  list-style: none;
  margin: 0;
  padding: 0; }
  .list-inline__item {
    display: inline-block; }
    .list-inline__item:nth-child(4n) {
      margin-right: 0; }

.list-float__item {
  margin-right: 10px;
  padding-bottom: 10px; }
  .list-float__item:nth-child(2), .list-float__item:first-child {
    float: left;
    width: calc(35%/2); }
    @media (min-width: 768px) {
      .list-float__item:nth-child(2), .list-float__item:first-child {
        width: calc(100%/4 - 22px); } }
  .list-float__item:nth-child(3), .list-float__item:last-child {
    float: right;
    width: calc(35%/2); }
    @media (min-width: 768px) {
      .list-float__item:nth-child(3), .list-float__item:last-child {
        width: calc(100%/4 - 22px); } }
  .list-float__item:nth-child(3) {
    margin-right: 0; }

.task__title {
  font-size: 14px; }

.task-five {
  margin: 30px auto; }

.item {
  width: 100%;
  height: auto;
  border: 1px solid #cfcfcf;
  padding-left: 10px;
  padding-top: 10px;
  margin-bottom: 10px; }
  @media (min-width: 768px) {
    .item {
      width: calc(100%/4 - 22px);
      margin-right: 10px; } }

.item--reverse {
  align-self: center;
  padding-bottom: 10px; }

.item--inline {
  padding-bottom: 10px;
  box-sizing: border-box; }
  @media (min-width: 768px) {
    .item--inline {
      width: calc(100%/4 - 8px); } }

.table {
  width: 100%;
  border-collapse: collapse; }
  .table__item {
    border: 1px solid #cfcfcf;
    padding: 10px; }

.task-seven {
  margin-top: 130px; }

.task-eight {
  margin-top: 30px;
  margin-bottom: 30px; }

.task-eight__table {
  display: grid;
  grid-template-columns: repeat(4, auto); }

.task-eight__table .table__item:first-child,
.task-eight__table .table__item:nth-child(2),
.task-eight__table .table__item:nth-child(2n+1),
.task-eight__table .table__item:nth-child(3n) {
  border-right: none; }

.task-eight__table .table__item:first-child,
.task-eight__table .table__item:nth-child(2),
.task-eight__table .table__item:nth-child(3),
.task-eight__table .table__item:nth-child(4) {
  border-bottom: none; }

.task-nine {
  margin-bottom: 30px; }

.task-nine__item {
  display: none;
  background-color: #ededf1;
  padding: 8px 30px;
  text-align: center;
  color: #383736;
  line-height: 20px;
  font-weight: 400; }

.task-nine__item p {
  margin: 0; }
